import React, { Component } from "react";
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

import pic1 from './../assets/images/AIA.jpg'
import pic2 from './../assets/images/pic16.png'
import pic3 from './../assets/images/gold.jpg'

export default class Example extends Component {

    render() {
        const responsive = {
            superLargeDesktop: {
                // the naming can be any, depends on you.
                breakpoint: { max: 4000, min: 3000 },
                items: 3
            },
            desktop: {
                breakpoint: { max: 3000, min: 1024 },
                items: 3
            },
            tablet: {
                breakpoint: { max: 1024, min: 464 },
                items: 2
            },
            mobile: {
                breakpoint: { max: 464, min: 0 },
                items: 1
            }
        };
        const data = [
            {
                id: 1,
                url: pic1,
                title: "Crypto-Pills",
                detail: "Micha Klein's virtual medicine for a sick society",
                btn: "live"
            },
            {
                id: 1,
                url: pic2,
                title: "Crypto-Pills",
                detail: "Micha Klein's virtual medicine for a sick society",
                btn: "live"
            },
            {
                id: 1,
                url: pic3,
                title: "Crypto-Pills",
                detail: "Micha Klein's virtual medicine for a sick society",
                btn: "live"
            }
            , {
                id: 1,
                url: pic1,
                title: "Crypto-Pills",
                detail: "Micha Klein's virtual medicine for a sick society",
                btn: "live"
            }
        ]
        return (
            <>
                <h1 className="text-center">Exclusive OpenSea drops</h1>


                <Carousel className=""
                    swipeable={false}
                    draggable={false}
                    showDots={true}
                    responsive={responsive}
                    ssr={true} // means to render carousel on server-side.
                    infinite={true}
                    autoPlay={this.props.deviceType !== "mobile" ? true : false}
                    autoPlaySpeed={1000}
                    keyBoardControl={true}
                    customTransition="all .5"
                    transitionDuration={500}
                    containerClass="carousel-container"
                    removeArrowOnDeviceType={["tablet", "mobile"]}
                    deviceType={this.props.deviceType}
                    dotListClass="custom-dot-list-style"
                    itemClass="carousel-item-padding-40-px"
                >

                    {
                        data.map((item) => {
                            return (
                                <div className="carouselImg card">
                                    <img src={item.url} alt="" />
                                    <div className="imgContent">
                                        <h3>{item.title}</h3>
                                        <p>{item.detail}</p>
                                        <a href="/">{item.btn}</a>
                                    </div>
                                </div>
                            )
                        })
                    }


                </Carousel>
                <section>
                   <div className="d-flex">
                   <h1> Section2        
                    </h1>
                    <div class="dropdown">
                        <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
                            Dropdown button
                        </button>
                        <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                            <li><a className="dropdown-item" href="/">Action</a></li>
                            <li><a className="dropdown-item" href="/">Another action</a></li>
                            <li><a className="dropdown-item" href="/">Something else here</a></li>
                        </ul>
                    </div>
                   </div>
                   
                <Carousel className=""
                    swipeable={false}
                    draggable={false}
                    showDots={true}
                    responsive={responsive}
                    ssr={true} // means to render carousel on server-side.
                    infinite={true}
                    autoPlay={this.props.deviceType !== "mobile" ? true : false}
                    autoPlaySpeed={1000}
                    keyBoardControl={true}
                    customTransition="all .5"
                    transitionDuration={500}
                    containerClass="carousel-container"
                    removeArrowOnDeviceType={["tablet", "mobile"]}
                    deviceType={this.props.deviceType}
                    dotListClass="custom-dot-list-style"
                    itemClass="carousel-item-padding-40-px"
                >

                    {
                        data.map((item) => {
                            return (
                                <div className="carouselImg card">
                                    <img src={item.url} alt="" />
                                    <div className="imgContent">
                                        <h3>{item.title}</h3>
                                        <p>{item.detail}</p>
                                        <a href="/">{item.btn}</a>
                                    </div>
                                </div>
                            )
                        })
                    }


                </Carousel>
                </section>
            </>
        )
    }
};