import React from 'react'
import pic17 from '../assets/images/pic17.png'
// import pic17 from '../assets/images/pic17.png'
import pic13 from '../assets/images/pic13.png'
import pic6 from '../assets/images/pic6.png'
import icon22 from '../assets/images/icon22.png'
import icon23 from '../assets/images/icon23.png'
import icon24 from '../assets/images/icon24.png'
import icon25 from '../assets/images/icon25.png'
import icon33 from '../assets/images/icon33.png'
import icon34 from '../assets/images/icon34.png'
import icon35 from '../assets/images/icon35.png'
import icon36 from '../assets/images/icon36.png'

import neymar from '../assets/images/neymar.jpg'
import Creators from './Creators'
const Section3 = () => {
    const data = [{
        id: 1,
        img: neymar,
        icon: icon23,
        title: "Ballon D'or 21",

    },
    {
        id: 2,
        img: pic6,
        icon: icon24,
        title: "King Kante"

    },
    {
        id: 3,
        img: pic13,
        icon: icon25,
        title: "Football coin 3D print"

    }
    ]
    const creators = [
        {
            id: 1,
            icon: icon33,
            title: "Hunter Ray",
            btn: "2.456BNB",
            number: "3"
        },
        {
            id: 2,
            icon: icon34,
            title: "Brittney Bradly",
            btn: "2.456BNB",
            number: "6"
        },
        {
            id: 3,
            icon: icon35,
            title: "Brenda Jackson",
            btn: "2.456BNB",
            number: "2"
        }
        ,
        {
            id: 4,
            icon: icon36,
            title: "Lorena Ledner",
            btn: "2.456BNB",
            number: "4"
        }
    ]
    return (
        <div>
            <div className="container-fluid section3">
                <div className="row">
                    <div className="col-lg-4  left_side">
                        <div className="card">
                            <img src={pic17} alt="" />
                            <div className="card-body d-flex">
                                <img src={icon22} alt="" />
                                <div className="left">
                                    <h5> <a href="/">The future of BNB </a> </h5>
                                    <p>18 in stock</p>
                                </div>
                                <div className="right">
                                    <span>highest paid</span>
                                    <a href="/" className="green_btn">1.125B</a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4  middle">
                        {
                            data.map((item) => {
                                return (
                                    <div className=" middle_card my-3">
                                        <img src={item.img} alt="" className="middle_img" />
                                        <div className="card_right">
                                            <h6> {item.title}</h6>
                                            <div className="d-flex">
                                                <img src={item.icon} alt="" />
                                                <a href="/" className="green_btn">0.27BNB</a>
                                                <span> </span>
                                            </div>
                                            <button className="btn_bid ">Place a bid</button>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                    <div className="col-lg-2 latest_uploads ">
                        <h5>Latest upload from creators</h5>
                        {
                            creators.map((item) => {
                                return (
                                    <div className="icons d-flex my-3">
                                       <div className="imgIcon">
                                       <img src={item.icon} alt="  " />
                                       <span>{item.number}</span>
                                       </div>
                                        <div className="mx-3">
                                            <h5>{item.title}</h5>
                                            <a href="/" className="green_btn">{ item.btn}</a>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
                <Creators/>
            </div>
        </div>
    )
}

export default Section3
