import React from 'react'
import footerImg from '../assets/images/footerImg.PNG'

const Footer = () => {
    return (
        <div className="container-fluid footer">
            <div className="row">
                <div className="col-lg-4">
                    <div>
                        <img src={footerImg} alt="" />
                        <h5>The New Creative Economy.</h5>
                        <div className="darkMode_btn d-flex">
                            <p>Dark theme</p>
                            <button className="btn">toggle</button>
                        </div>
                    </div>
                </div>
                <div className="col-lg-8">
                    <div className="row">
                        <div className="col-lg-3">
                            <ul>
                                <li className="mb-5 dark_li" > <a href="www.closedsea.com">ClosedSea.</a></li>
                                <li className="my-3"> <a href="www.closedsea.com">Discover</a></li>
                                <li> <a href="www.closedsea.com">Connect Wallet</a></li>
                            </ul>
                        </div>
                        <div className="col-lg-3">
                            <ul>
                                <li className="mb-5 dark_li"><a href="/" >Info</a></li>
                                <li className="my-3"><a href="/">FAQ</a></li>
                                <li><a href="/">Create item</a></li>
                            </ul>
                        </div>
                        <div className="col-lg-6">
                            <div className="form">
                                <h6>Join Newsletter</h6>
                                <p>Subscribe our newsletter to get more free design course and resource</p>
                                <div className="form-group">
                                    <input type="email" placeholder="Enter Your Email" />
                                <i className="fas fa-arrow"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
               
            </div>
            <hr  className="my-5" />
            <div className="copyrights ">
                <p>Copyright © 2021 Closed Sea. All rights reserved</p>
                <p>We use cookies for better service. <a href="/">Accept</a></p>
            </div>
        </div>
    )
}

export default Footer
