import React from "react";

const Navbar = () => {
  return (
    <div>
    <nav className="navbar navbar-expand-lg navbar-light ">
    <div className="container-fluid">
    <a className="navbar-brand" href="/">CLOSEDSEA</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ">
        <li className="nav-item">
          <a className="nav-link active" aria-current="page" href="/">dicover</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href="/">how it works</a>
        </li>
        
      </ul>
      <form className="input">
        <div><input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
        <i className="fas fa-search"></i>
        </div>
    
      </form>

      <ul className="navbar-nav nav_left ">
        <li className="nav-item">
          <a className="nav-link active" href="/"><i className="far fa-bell"></i> </a>
        </li>
        <li className="nav-item">
        <button className="btn btn-upload">upload</button>
        </li>
        <li className="nav-item">
          <button className="btn btn-connect">connect wallet</button>
        </li>
      </ul>
    </div>
  </div>
</nav>
    </div>
  );
};

export default Navbar;
